n placeholder-image-size {\n    width: 81px;\n    height: 82px;\n\n    @include is-screen-size(desktop) {\n        width: 219px;\n        height: 119px;\n    }\n}\n\n@mixin placeholder-status-size {\n    width: 117px;\n    height: 78px;\n\n    @include is-screen-size(desktop) {\n        width: 117px;\n        height: 78px;\n    }\n}\n","@import \"mixins\";\n@import \"vars\";\n@import \"~@angular/material/theming\";\n\n.mck {\n    &-inherit {\n        &-font {\n            // patches global font styling done by legacy ui\n            font: inherit;\n        }\n    }\n\n    &-content {\n        &-expand {\n            width: 100%;\n            height: 100%;\n\n            &-width {\n                width: 100%;\n            }\n\n            &-height {\n                height: 100%;\n            }\n        }\n\n        &-with-footer-height {\n            height: calc(100vh - 58px);\n        }\n\n        &-full-height {\n            height: 100vh;\n        }\n\n        &-page {\n            @extend .mck-content-expand;\n            background: $page-background-color;\n\n            &-container {\n                @extend .mck-content-full-height;\n            }\n        }\n\n        &-body {\n            width: 100%;\n            @include is-screen-size(not-mobile) {\n                max-width: 1024px;\n            }\n        }\n\n        &-group {\n            @include page-padding;\n            width: 100%;\n\n            @include is-screen-size(not-mobile) {\n                max-width: 759px;\n            }\n        }\n    }\n\n    &-shadow {\n        @include mat-elevation(1);\n    }\n\n    &-flex-1 {\n        flex: 1;\n    }\n\n    &-row {\n        display: flex;\n        flex-direction: row;\n\n        &.mck-center {\n            &-justify {\n                justify-content: center;\n            }\n\n            &-align {\n                align-items: center;\n            }\n\n            @extend .mck-center-justify;\n            @extend .mck-center-align;\n        }\n\n        &.mck-stretch {\n            align-items: stretch;\n        }\n\n        &-flow {\n            @extend .mck-row;\n            flex-wrap: wrap;\n        }\n\n        &.mck-scroll {\n            overflow-x: scroll;\n        }\n    }\n\n    &-column {\n        display: flex;\n        flex-direction: column;\n\n        &.mck-center {\n            justify-content: center;\n            align-items: center;\n        }\n\n        &.mck-space-between {\n            justify-content: space-between;\n        }\n\n        &.mck-stretch {\n            align-items: stretch;\n        }\n\n        &.mck-scroll {\n            overflow-y: scroll;\n            overflow-y: overlay;\n        }\n\n        &.mck-flex-end {\n            justify-content: flex-end;\n        }\n    }\n\n    &-overflow {\n        overflow: auto;\n    }\n\n    &-bordered, &-input {\n        border-radius: 0;\n        border: 1px solid $border;\n\n        &:hover {\n            border-color: #464646;\n        }\n\n        &:focus {\n            border-color: $primary;\n        }\n\n        &[disabled=\"true\"] {\n            pointer-events: none;\n            border-color: #f0f0f0;\n            color: #ccc;\n        }\n    }\n\n    &-date-display {\n        position: relative;\n        display: flex;\n        align-items: center;\n        width: 100%;\n        height: 2.5em;\n        padding: $spacing-small $spacing-xxsmall;\n        transition: border 200ms, box-shadow 200ms;\n    }\n\n    &-form-input {\n\n        .mat-form-field-wrapper {\n            padding-bottom: $spacing-xxsmall;\n        }\n\n        mat-form-field {\n            min-width: auto !important;\n\n            .mat-form-field-infix {\n                width: 100%;\n                padding-top: $spacing-xxsmall !important;\n                padding-bottom: $spacing-xxsmall !important;\n            }\n\n            .mat-form-field-outline {\n                min-height: 2.5rem;\n            }\n\n            &.mck-center-suffix {\n                .mat-form-field-infix {\n                    @extend .mck-row;\n                    @extend .mck-center;\n                }\n            }\n\n            &.mck-select-dropdown-button {\n                .mat-form-field-infix {\n                    margin-top: 4px;\n                }\n\n                .mat-form-field-flex {\n                    @include is-screen-size(mobile) {\n                        padding-left: $spacing-xxsmall;\n                        padding-right: $spacing-xxsmall;\n                    }\n                }\n\n                @include is-screen-size(mobile) {\n                    .mat-form-field-outline, .mat-form-field-infix {\n                        height: 2rem;\n                        min-height: 2rem;\n                    }\n\n                    .mat-form-field-infix {\n                        padding-top: 0 !important;\n                        padding-bottom: 0 !important;\n                    }\n                }\n            }\n        }\n\n        mat-form-field.mat-s {\n            @include form-label;\n            text-align: left;\n        }\n\n        mat-select {\n            margin-top: 0.2rem;\n\n            .mat-select-arrow {\n                visibility: hidden;\n            }\n\n            & .mat-select-value > .mat-select-value-text > span {\n                @include form-label;\n                font-weight: 400;\n            }\n        }\n\n        .mat-form-field-outline {\n            & > .mat-form-field-outline-start.mat-form-field-outline-start,\n            & > .mat-form-field-outline-gap.mat-form-field-outline-gap,\n            & > .mat-form-field-outline-end.mat-form-field-outline-end {\n                border-color: $border;\n                border-width: 1px;\n                border-radius: 0;\n            }\n        }\n\n        .mat-form-field-outline-thick {\n            & > .mat-form-field-outline-start.mat-form-field-outline-start,\n            & > .mat-form-field-outline-gap.mat-form-field-